<template>
  <div class="countries">
    <Welcome msg="Här hittar du alla länder" />

    <b-button block variant="success" v-on:click="fetchCountries"
      >Hämta alla länder</b-button
    >
    <p></p>
    <ul v-if="countries">
      <li v-for="country in countries" :key="country.id">
        {{ country.name }}, {{ country.region }}
      </li>
    </ul>
  </div>
</template>

<script>
import Welcome from "@/components/welcomeCountriesView.vue";
import axios from "axios";

export default {
  data() {
    return {
      countries: null,
    };
  },
  name: "Countries",
  components: {
    Welcome,
  },
  methods: {
    fetchCountries() {
      axios.get("https://restcountries.eu/rest/v2/all").then((response) => {
        this.countries = response.data;
      });
    },
  },
  mounted() {},
};
</script>
